* {
    box-sizing: border-box;
  }
  
  *,
  *::after,
  *::before {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  ul,
  p {
    margin: 0;
    padding: 0;
  }
  
  button {
    padding: 0;
  }
  
  svg,
  img {
    display: block;
    max-width: 100%;
  }
  
  input {
    cursor: pointer;
  }
  
  
  a {
    display: inline-block;
    text-decoration: none;
  }
  
  ul {
    list-style: none;
  }